<template>
    <div class="MonHocToan">
        <raw-loader :mytxt="require('!raw-loader!./TestTree.vue').default"></raw-loader>
        <h1>List page</h1>
        <div>
            <ul>
                <li v-for="x in ds" :key="x.id">
                    {{ x }}
                    <button @click="onEdit(x)">Edit</button>
                </li>
            </ul>

        </div>
    </div>
</template>
<script>
export default {
    name: 'Tpl',
    components: {
    },
    data() {
        return {
            ds: [
                { id: 1, ten: "Ba", pid: 0 },
                { id: 2, ten: 'teo', pid: 1 },
                { id: 3, ten: 'ti', pid: 1 },
                { id: 4, ten: 'vu', pid: 1 },
                { id: 5, ten: 'xuka', pid: 3 },
                { id: 6, ten: 'tini', pid: 3 },
            ]
        }
    },
    mounted(){
        this.bdf();
    },
    methods: {
        getItemById(ds,id){
            let index=ds.findIndex(x=>x.id==id);
            if( index <0 ) return null;
            return ds[index];
        },
        bdf(){
            let s=[];
            let daduyet=[];
            let start=1;
            let level=0;
            daduyet.push(start);
            s.push(this.getItemById(this.ds,start));
            while(s.length!=0){
                let layra=s.pop();
                console.log(`${JSON.stringify(layra)} - level : ${level}`);
                let count=0;
                this.ds.forEach(x=>{
                    if(x.pid==layra.id && daduyet.includes(x.id)==false  ){
                        s.push(x);
                        count++;
                    }
                })
                if(count==0){
                    level=0;
                }else{
                    level++;
                }
            }
        }
    }
}
</script>
  